import React, { Component } from "react";
import { Link } from 'react-router-dom';

export default class PostBlog extends Component {

  getCategory = (categoryPost = "") => {
    switch (categoryPost.toLowerCase()) {
      case "seo":
        return <SEO />;
  
      case "googleadwords":
        return <GoogleAdwords />;
  
      case "design":
        return <Design />;
  
      default:
        return <MarketingDigital />;
    }
  }

  render() {
    return (
      <Link to={"/blog-interno/" + this.props._id}>
      <div className="img-responsive">
        <figure>
          <img src={this.props.imgUrl} alt="" />
        </figure>
      </div>
      {this.getCategory(this.props.categoryPost)}
      <h3>{this.props.title}</h3>
      <p>{this.props.description}</p>
    </Link>
    )
  }
}

/* Componentes Category */

function MarketingDigital(props) {
  return (
    <div className="category category-blue-cloud">
      <span>Marketing Digital</span>
    </div>
  );
}

function SEO(props) {
  return (
    <div className="category category-magenta-violet">
      <span>SEO</span>
    </div>
  );
}

function Design(props) {
  return (
    <div className="category category-dark-blue">
      <span>Design</span>
    </div>
  );
}

function GoogleAdwords(props) {
  return (
    <div className="category category-graphite">
      <span>Google Adwords</span>
    </div>
  );
}

/* Fim Componentes Category */



