import React, { Fragment } from "react";
import NavBar from "./../layouts/common/NavBar/NavBar";
import BannerTitle from "./../layouts/common/BannerTitle/BannerTitle";
import Breadcrumb from "./../layouts/common/Breadcrumb/Breadcrumb";
import { Link } from "react-router-dom";
import RedesSociais from "./../layouts/common/RedesSociais/RedesSociais";
import Footer from "./../layouts/common/Footer/Footer";
import MapaSite from "./../layouts/common/MapaSite/MapaSite";
import PostBlog from "./../layouts/Blog/PostBlog";
import PageComponent from "../core/PageComponent";

const description = `Lorem ipsum dolor sit amet, consectetur adipiscing elit. Phasellus
eu laoreet augue. Pellentesque dapibus metus non ipsum bibendum, eu
maximus tellus posuere. Orci varius natoque penatibus et magnis dis
parturient montes, nascetur ridiculus mus. Nam viverra cursus justo
in lobortis. Praesent semper felis eu ultrices finibus. Nunc at
pretium nibh.`;

const posts = [
  {
    id: 1,
    imgUrl: "img/blog1.jpg",
    categoryName: "SEO",
    title: "Lorem ipsum dolor sit amet",
    description,
    link: "/blog-interno"
  },
  {
    id: 2,
    imgUrl: "img/blog2.jpg",
    categoryName: "Design",
    title: "Lorem ipsum dolor sit amet",
    description,
    link: "/blog-interno"
  },
  {
    id: 3,
    imgUrl: "img/blog3.jpg",
    categoryName: "GoogleAdwords",
    title: "Lorem ipsum dolor sit amet",
    description,
    link: "/blog-interno"
  }
];

export default class Blog extends PageComponent {
  render() {
    return (
      <Fragment>
        {/* <!-- Configurção landscape --> */}
        <div id="lying" />

        {/* <!-- Configurção natural --> */}
        <div id="standing">
          <main id="wrapper-main" className="page-inside blog">
            
              <NavBar />
            

            <section id="wrapper-banner">
              <BannerTitle>
                <h1>
                  <strong>Blog</strong>
                </h1>
              </BannerTitle>
            </section>

            <section id="wrapper-master">
              <Breadcrumb>
                <li>
                  <Link to="/">dígitozero</Link>
                </li>
                <li>
                  <span>Blog</span>
                </li>
              </Breadcrumb>

              <div className="section blog">
                <div className="container">
                  <div className="row">
                    <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
                      <div className="row">
                        <div className="col-xl-9 col-lg-9 col-md-12 col-sm-12 col-12">
                          <div className="news posts-destaques">
                            <article className="post-principal">
                              <PostBlog
                                imgUrl="img/blog1.jpg"
                                categoryName="MarketingDigital"
                                title="Lorem ipsum dolor sit amet"
                                description={description}
                                link={"/blog-interno"}
                              />
                            </article>

                            {/* TODO Colocar em um único map */}
                            <div className="row">
                              {posts.map(post => {
                                return (
                                  <div
                                    key={post.id}
                                    className="col-xl-4 col-lg-4 col-md-12 col-sm-12 col-12"
                                  >
                                    <div
                                      key={post.id}
                                      className="wrapper-content news"
                                    >
                                      <PostBlog key={post.id} {...post} />
                                    </div>
                                  </div>
                                );
                              })}
                            </div>

                            {/* TODO Colocar em um único map */}
                            <div className="row">
                              {posts.map(post => {
                                return (
                                  <div
                                    key={post.id}
                                    className="col-xl-4 col-lg-4 col-md-12 col-sm-12 col-12"
                                  >
                                    <div
                                      key={post.id}
                                      className="wrapper-content news"
                                    >
                                      <PostBlog key={post.id} {...post} />
                                    </div>
                                  </div>
                                );
                              })}
                            </div>
                          </div>
                        </div>
                        <div
                          className="col-xl-3 col-lg-3 col-md-12 col-sm-12 col-12"
                          id="sidebar-blog"
                        >
                          <PostCategory />
                          <LastPosts />
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </section>

            <section id="wrapper-map" className="featured">
              <MapaSite />
            </section>

            <section id="wrapper-social-networks">
              <RedesSociais />
            </section>

            <footer id="wrapper-footer">
              <Footer />
            </footer>
          </main>
        </div>
      </Fragment>
    );
  }
}

function LastPosts(props) {
  return (
    <Fragment>
      <h2>
        <span>Últimos Posts</span>
      </h2>

      <ul className="lasts-posts">
        <li>
          <Link to="/">
            Lorem ipsum dolor sit amet, consectetur adipiscing elit. Phasellus
            eu laoreet a...
          </Link>
        </li>
        <li>
          <Link to="/">
            Lorem ipsum dolor sit amet, consectetur adipiscing elit. Phasellus
            eu laoreet a...
          </Link>
        </li>
        <li>
          <Link to="/">
            Lorem ipsum dolor sit amet, consectetur adipiscing elit. Phasellus
            eu laoreet a...
          </Link>
        </li>
      </ul>
    </Fragment>
  );
}

function PostCategory(props) {
  return (
    <Fragment>
      <h2 className="text-left">
        <span>Categorias</span>
      </h2>
      <ul className="list-category">
        <li>
          <Link to="/blog">
            <div className="category category-dark-blue">
              <span>Design</span>
            </div>
          </Link>
        </li>
        <li>
          <Link to="/blog">
            <div className="category category-blue-cloud">
              <span>Marketing Digital</span>
            </div>
          </Link>
        </li>
        <li>
          <Link to="/blog">
            <div className="category category-magenta-violet">
              <span>SEO</span>
            </div>
          </Link>
        </li>
        <li>
          <Link to="/blog">
            <div className="category category-graphite">
              <span>Google Adwords</span>
            </div>
          </Link>
        </li>
      </ul>
    </Fragment>
  );
}



