import React, { Component } from "react";

class VideoBackground extends Component {
  render() {
    return (
    <video autoPlay loop poster="/img/d0-conect.jpg" className="bg-video" >
        <source src="/video/d0-conect-1920.webm" type="video/webm" />
        <source src="/video/d0-conect-1920.mp4" type="video/mp4" />
      </video>
    );
  }
}

export default VideoBackground;



