import React, { Component } from "react";

class BannerTitle extends Component {
  render() {
    return (
      <div id="banner-title">
        <div className="container">
          <div className="row">
            <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
              {this.props.children}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default BannerTitle;



