import React, { Fragment } from "react";
import NavBar from "./../layouts/common/NavBar/NavBar";
import MapaSite from "./../layouts/common/MapaSite/MapaSite";
import RedesSociais from "./../layouts/common/RedesSociais/RedesSociais";
import BannerTitle from "./../layouts/common/BannerTitle/BannerTitle";
import Footer from "./../layouts/common/Footer/Footer";
import PostsRelacionados from "./../layouts/BlogInterno/PostsRelacionados";
import AutorPost from "./../layouts/BlogInterno/AutorPost";
import PageComponent from "../core/PageComponent"

export default class BlogInterno extends PageComponent {
  render() {
    return (
      <Fragment>
        {/* <!-- Configurção landscape --> */}
        <div id="lying" />

        {/* <!-- Configurção natural --> */}
        <div id="standing">
          <div id="wrapper-main" className="opening blog blog-inside">

            
              <NavBar />
            

            <main>
              <article>
                <section id="wrapper-banner">
                  <figure className="bg-blog">
                    <img src="img/blog1.jpg" alt="Texto do Blog" />
                  </figure>

                  <BannerTitle>
                    <h1>
                      Lorem ipsum dolor sit amet, consectetur adipiscing elit.
                      Sed id neque ut turpis
                    </h1>
                  </BannerTitle>
                </section>

                <section id="wrapper-master">
                  <div className="section section-blog">
                    <div className="container">
                      <AutorPost />
                      <div className="content-post">
                        <h4>
                          Lorem ipsum dolor sit amet, consectetur adipiscing
                          elit. Morbi efficitur neque sed sapien porta, vel
                          varius nisl faucibus. Pellentesque pharetra ante
                          massa, nec volutpat dui maximus a.
                        </h4>
                        <p>
                          Lorem ipsum dolor sit amet, consectetur adipiscing
                          elit. Morbi efficitur neque sed sapien porta, vel
                          varius nisl faucibus. Pellentesque pharetra ante
                          massa, nec volutpat dui maximus a. Suspendisse
                          facilisis, erat ut ultrices elementum, metus risus
                          vulputate erat, id finibus velit leo id nunc. Proin
                          porttitor aliquam gravida. Praesent commodo eros
                          sapien, ut malesuada odio molestie eu. Ut condimentum
                          mi quis purus tempus condimentum. Nam varius sapien
                          quis lacus congue auctor. Nulla eget volutpat magna.
                          Suspendisse rhoncus urna id orci molestie, eget
                          tincidunt urna ultricies. Aliquam erat volutpat.
                          Integer ligula turpis, mattis quis ultrices ut, auctor
                          quis odio. Sed finibus imperdiet purus, id feugiat
                          elit congue a. Nam sollicitudin ex vel nunc molestie
                          sollicitudin. Nullam congue leo et tortor tincidunt
                          condimentum.
                        </p>
                        <p>
                          Etiam elementum, enim in elementum malesuada, risus
                          tortor rutrum quam, a sollicitudin ante quam in magna.
                          Nulla facilisi. Ut nec urna sit amet odio rutrum
                          mattis vitae vitae enim. Pellentesque vel ante nisi.
                          Class aptent taciti sociosqu ad litora torquent per
                          conubia nostra, per inceptos himenaeos. Etiam enim
                          velit, mattis ac vestibulum at, faucibus ut nibh. Sed
                          interdum molestie lacus id efficitur. Vestibulum eu
                          efficitur mauris, et posuere augue.
                        </p>
                      </div>
                      <PostsRelacionados />
                    </div>
                  </div>
                </section>
              </article>

              <section id="wrapper-map" className="featured">
                <MapaSite />
              </section>

              <section id="wrapper-social-networks">
                <RedesSociais />
              </section>

              <footer id="wrapper-footer">
                <Footer />
              </footer>
            </main>
            
          </div>
        </div>
      </Fragment>
    );
  }
}



