import React, { Fragment } from "react";
import BannerTitle from "../../layouts/common/BannerTitle/BannerTitle";
import Breadcrumb from "../../layouts/common/Breadcrumb/Breadcrumb";
import NavBar from "../../layouts/common/NavBar/NavBar";
import MapaSite from "../../layouts/common/MapaSite/MapaSite";
import RedesSociais from "../../layouts/common/RedesSociais/RedesSociais";
import Footer from "../../layouts/common/Footer/Footer";
import { Link } from "react-router-dom";
import PageComponent from "../../core/PageComponent";
import FormOrcamentoServico from "./../../forms/common/FormOrcamentoServico";
import DevelopmentSVG from "../Development/DevelopmentSVG";

export default class Design extends PageComponent {
  render() {
    return (
      <Fragment>
        {/* <!-- Configurção landscape --> */}
        <div id="lying" />

        {/* <!-- Configurção natural --> */}
        <div id="standing">
          <div id="wrapper-main" className="page-inside branding">
            
              <NavBar />
            

            <main>
              <section id="wrapper-banner">
                <BannerTitle>
                  <h1>
                    <strong>Design</strong>
                  </h1>
                </BannerTitle>
              </section>

              <section id="wrapper-master">
                <Breadcrumb>
                  <li>
                    <Link to="/">dígitozero</Link>
                  </li>
                  <li>
                    <span>Design</span>
                  </li>
                </Breadcrumb>

                {/* <div className="master">
                  <div className="container">
                    <div className="row">
                      <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
                        <div className="wrapper-content">
                          <p>

                          </p>
                        </div>
                      </div>
                    </div>
                  </div>
                </div> */}

                <div className="container">
                  <div className="row">
                    <div className="col-xl-6 col-lg-6 col-md-12 col-sm-12 col-12">
                      <div className="wrapper-content">
                        <span className="icon branding">
                          <DevelopmentSVG />
                        </span>
                      </div>
                    </div>
                    <div className="col-xl-6 col-lg-6 col-md-12 col-sm-12 col-12">
                      <div className="wrapper-content">
                      <h2>Design</h2>
                        <p>
                          A equipe de Design da dígitozero é multidisciplinar,
                          criativa e está sempre antenada a toda e qualquer
                          tendência do mercado. Realizamos projetos de branding
                          de diversos tipos, seja qual for a necessidade. Crie
                          uma logomarca marcante para a sua empresa Planejamos a
                          identidade visual. Fazemos sites de acordo com as
                          melhores práticas de UX, Garantimos a melhor
                          usabilidade possível para quem navega
                        </p>
                      </div>
                    </div>
                  </div>
                </div>

                <div className="section">
                  <div className="container">
                    <div className="row">
                    </div>
                  </div>
                </div>
              </section>

              <section id="wrapper-map" className="featured">
                <MapaSite />
              </section>

              <section id="wrapper-social-networks">
                <RedesSociais />
              </section>
            </main>

            <footer id="wrapper-footer">
              <Footer />
            </footer>
          </div>
        </div>
      </Fragment>
    );
  }
}



