import React, { Fragment } from "react";
import "./App.scss";
import NavBar from "./../../layouts/common/NavBar/NavBar";
import MapaSite from "./../../layouts/common/MapaSite/MapaSite";
import RedesSociais from "./../../layouts/common/RedesSociais/RedesSociais";
import Footer from "./../../layouts/common/Footer/Footer";
import { Link } from "react-router-dom";
import BannerTitle from "./../../layouts/common/BannerTitle/BannerTitle";
import NoticiasBlog from "./../../layouts/App/NoticiasBlog/NoticiasBlog";
import Solucoes from "./../../layouts/App/Solucoes/Solucoes";
import VideoBackground from "./../../layouts/App/VideoBackground/VideoBackground";
import Apresentacao from "./../../layouts/App/Apresentacao/Apresentacao";
import PageComponent from "./../../core/PageComponent";

import { Query } from "react-apollo";
import gql from "graphql-tag";

const description = `Lorem ipsum dolor sit amet, consectetuer adipiscing
elit, sed diam nonummy nibh euismod tincidunt ut
laoreet dolore magna aliquam erat volutpat.`;

class App extends PageComponent {

  render() {
    return (
      <Fragment>
        {/* Configurção landscape */}
        <div id="lying" />

        {/* Configurção natural */}
        <div id="standing">
          <div id="wrapper-main" className="opening">
            
              <NavBar />
            

            {/* O dígito que faltava na sua estratégia de Marketing Digital */}
            <main>
              <section id="wrapper-banner">
                <VideoBackground />
                <BannerTitle>
                  <h1>
                    <strong>Aumente</strong> seus <strong>resultados</strong>{" "}
                    com <strong>Marketing Digital</strong>
                  </h1>
                </BannerTitle>
              </section>

              {/* Venha para a dígitozero */}
              <section id="wrapper-master">
                <div className="app-master">
                  <div className="container">
                    <div className="row">
                      <Apresentacao />
                    </div>
                  </div>
                </div>

                {/* Soluções: Design, Inbound, Ads */}
                <div className="featured">
                  <div className="container">
                    <div className="row">
                      <Solucoes />
                    </div>
                  </div>
                </div>

                <div className="section">
                  <div className="container">
                    {/* <div className="row">
                      <FetchLastNews />
                      <FetchTopNews />

                      <div className="col-12">
                        <Link to="/blog" className="button area">
                          Confira nosso Blog
                        </Link>
                      </div>
                    </div> */}
                  </div>
                </div>
              </section>

              <section id="wrapper-map" className="featured">
                <MapaSite />
              </section>

              <section id="wrapper-social-networks">
                <RedesSociais />
              </section>
            </main>

            <footer id="wrapper-footer">
              <Footer />
            </footer>
          </div>
        </div>
      </Fragment>
    );
  }
}

export default App;

function FetchLastNews() {
  return (
    <Query
      query={gql`
        {
          lastNews{
            title
            blogPostPreview{
              title
              description
              categoryPost
              imgUrl
              _id
            }
          }
        }
      `}
    >
      {({ loading, error, data }) => {
        if (loading) return <p>Loading...</p>;
        if (error) return <p>Error :(</p>;
        return <NoticiasBlog {...data.lastNews} />
      }}
    </Query>
  );

}

function FetchTopNews() {
  return (
    <Query
      query={gql`
        {
          topNews{
            title
            blogPostPreview {
              title
              description
              categoryPost
              imgUrl
              _id
            }
          }
        }
      `}
    >
      {({ loading, error, data }) => {
        if (loading) return <p>Loading...</p>;
        if (error) return <p>Error :(</p>;
        return <NoticiasBlog {...data.topNews} />
      }}
    </Query>
  );

}


