import React, { Component, Fragment } from 'react';
import { Link } from 'react-router-dom';
import DesignSVG from './../../../SVG/DesignSVG';
import InboundSVG from './../../../SVG/InboundSVG';
import AdsSVG from './../../../SVG/AdsSVG';


class Solucoes extends Component {
    render() {
        return (
          <Fragment>
             {/* Design */}
              <div className="col-xl-4 col-lg-4 col-md-12 col-sm-12 col-12">
                <div className="wrapper-content border">
                  <div className="icon design">
                    <DesignSVG />
                  </div>
                  <h3>
                    <Link to="/design">Design</Link>
                  </h3>
                  <ul className="oppening-list">
                    <li>
                      <Link to="/branding">Branding</Link>
                    </li>
                    <li>
                      <Link to="/site">Site</Link>
                    </li>
                    <li>
                      <Link to="/loja-virtual">Loja Virtual</Link>
                    </li>
                    <li>
                      <Link to="/blog-corporativo">Blog Corporativo</Link>
                    </li>
                    <li>
                      <Link to="/ux">UX</Link>
                    </li>
                  </ul>
                  <button className="button">Soluções Design</button>
                </div>
              </div>
              {/* Fim Design */}

              {/* Inbound */}
              <div className="col-xl-4 col-lg-4 col-md-12 col-sm-12 col-12">
                <div className="wrapper-content border">
                  <div className="icon inbound">
                    <InboundSVG />
                  </div>
                  <h3>
                    <Link to="/inbound">Inbound</Link>
                  </h3>
                  <ul className="oppening-list">
                    <li>
                      <Link to="/seo">SEO</Link>
                    </li>
                    <li>
                      <Link to="/marketing-conteudo">SEO para E-commerce</Link>
                    </li>
                    <li>
                      <Link to="/google-analytics">Google Analytics</Link>
                    </li>
                    <li>
                      <Link to="/email-marketing">E-mail Marketing</Link>
                    </li>
                    <li>
                      <Link to="/google-tag-manager">Google Tag Manager</Link>
                    </li>
                  </ul>
                  <button className="button">Soluções SEO</button>
                </div>
              </div>
              {/* Fim Inbound */}

              {/* Ads */}
              <div className="col-xl-4 col-lg-4 col-md-12 col-sm-12 col-12">
                <div className="wrapper-content border">
                  <div className="icon ads">
                    <AdsSVG />
                  </div>
                  <h3>
                    <Link to="/ads">Ads</Link>
                  </h3>
                  <ul>
                    <li>
                      <Link to="/google-adwords">Google Adwords</Link>
                    </li>
                    <li>
                      <Link to="/facebook-ads">Facebook Ads</Link>
                    </li>
                    <li>
                      <Link to="/youtube-ads">Youtube Ads</Link>
                    </li>
                    <li>
                      <Link to="/linkedin-ads">LinkedIn Ads</Link>
                    </li>
                    <li>
                      <Link to="/ads/remarketing">Remarketing</Link>
                    </li>
                  </ul>
                  <button className="button">Soluções Ads</button>
                </div>
              </div>

              {/* Fim Ads */}
              </Fragment>
        );
    }
}

export default Solucoes;


