import React from "react";
import ReactDOM from "react-dom";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import "./index.scss";
import "bootstrap/scss/bootstrap.scss";
import "./animate.min.css";
import "./common.sass";
// import registerServiceWorker from "./registerServiceWorker";

//Pages
import App from "./components/pages/App/App";
import Empresa from "./components/pages/Empresa";
import Orcamento from "./components/pages/Orcamento";
import Blog from "./components/pages/Blog";
import BlogInterno from "./components/pages/BlogInterno";
import NotFound from "./components/pages/NotFound";
import EmailMarketing from "./components/pages/Inbound/EmailMarketing";
import GoogleAnalytics from "./components/pages/Inbound/GoogleAnalytics";
import GoogleTagManager from "./components/pages/Inbound/GoogleTagManager";
import MarketingConteudo from "./components/pages/Inbound/MarketingConteudo";
import SEO from "./components/pages/Inbound/SEO";
import BlogCorporativo from "./components/pages/Inbound/BlogCorporativo";
import YoutubeAds from "./components/pages/OnlineAdvertising/YoutubeAds"; 
import Remarketing from "./components/pages/OnlineAdvertising/Remarketing";
import LinkedinAds from "./components/pages/OnlineAdvertising/LinkedinAds";
import GoogleAdwords from "./components/pages/OnlineAdvertising/GoogleAdwords";
import FacebookAds from "./components/pages/OnlineAdvertising/FacebookAds";
import SponsoredLinks from "./components/pages/OnlineAdvertising/SponsoredLinks";
import Branding from "./components/pages/Design/Branding";
import UX from "./components/pages/Design/UX";
import LojaVirtual from "./components/pages/Development/LojaVirtual";
import Site from "./components/pages/Development/Site";
// import UI from "./components/pages/Design/UI";
import AppService  from './components/pages/Development/App';
import Design from "./components/pages/Design/Design";
import Inbound from './components/pages/Inbound/Inbound';
import Development from './components/pages/Development/Development';
import OnlineAdvertising from './components/pages/OnlineAdvertising/OnlineAdvertising';

// import ApolloClient from "apollo-boost";
// import { ApolloProvider } from "react-apollo";

// const client = new ApolloClient({
//   uri: "http://localhost:4000/graphql"
// });

ReactDOM.render(
  // <ApolloProvider client={client}>
    <Router>
      <Switch>
        <Route exact path="/" component={App} />
        <Route exact path="/empresa" component={Empresa} />
        <Route exact path="/price" component={Orcamento} />
        {/* <Route exact path="/blog" component={Blog} />
        <Route exact path="/online-advertising/blog-interno" component={BlogInterno} /> */}

        {/* Online Advertising */}
        <Route exact path="/online-advertising" component={OnlineAdvertising} />
        <Route exact path="/online-advertising/google-adwords" component={GoogleAdwords} />
        <Route exact path="/online-advertising/sponsored-links" component={SponsoredLinks} />
        <Route exact path="/online-advertising/facebook-ads" component={FacebookAds} />
        {/* <Route exact path="/online-advertising/instagram-ads" component={????} /> */}
        <Route exact path="/online-advertising/linkedin-ads" component={LinkedinAds} />
        <Route exact path="/online-advertising/remarketing" component={Remarketing} />
        {/* <Route exact path="/online-advertising/youtube-ads" component={YoutubeAds} /> */}

        {/* Inbound Marketing */}
        <Route exact path="/inbound-marketing" component={Inbound} />
        <Route exact path="/inbound-marketing/seo" component={SEO} />
        <Route exact path="/inbound-marketing/content-marketing" component={MarketingConteudo} />
        <Route exact path="/inbound-marketing/corporate-blog" component={BlogCorporativo} />
        <Route exact path="/inbound-marketing/email-marketing" component={EmailMarketing} />
        <Route exact path="/inbound-marketing/google-analytics" component={GoogleAnalytics} />
        {/* <Route exact path="/google-tag-manager" component={GoogleTagManager} /> */}

        {/* Design */}
        <Route exact path="/design" component={Design} />
        <Route exact path="/design/branding" component={Branding} />
        <Route exact path="/design/ux" component={UX} />
        {/* <Route exact path="/ui" component={UI} /> */}
        
        {/* Development */}
        <Route exact path="/development" component={Development} />
        <Route exact path="/development/site" component={Site} />
        <Route exact path="/development/e-commerce" component={LojaVirtual} />
        <Route exact path="/development/app" component={AppService} />

        <Route exact path="**" component={NotFound} />
      </Switch>
    </Router>
  // </ApolloProvider>
  ,
  document.getElementById("root")
);
// registerServiceWorker();



