import React, { Component } from "react";
// import gql from "graphql-tag";
// import { Mutation } from "react-apollo";

// const ADD_TODO = gql`
//   mutation orcamento($form: Form!){
//     orcamento(form: $form)
//   }
// `;

class ComponentFormOrcamentoServico extends Component {

  constructor(props){
    super(props)
    this.state = {
      name:"Kelvin",
      lastName:"Floresta",
      email:"kelvinf.contato@gmail.com",
      site:"http://digitozero.herokuapp.com",
      company:"Digito Zero",
      need:"Contruir um website com React"
    }
  }

  render() {
    return (
      <form className="form-default" onSubmit={this.props.onSubmit}>
        <div className="container">
          <div className="row">
            <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
              <div className="row">
                <div className="col-xl-6 col-lg-6 col-md-12 col-sm-12 col-12">
                  <div className="wrapper-content form-group">
                    <label htmlFor="txt-nome" className="lbl-title">
                      Seu nome?*
                    </label>
                    <input onChange={this.handleChange}
                      type="text"
                      name="name"
                      value={this.state.name}
                      className="form-control border"
                      id="txt-nome"
                      required
                    />
                  </div>
                </div>
                <div className="col-xl-6 col-lg-6 col-md-12 col-sm-12 col-12">
                  <div className="wrapper-content form-group">
                    <label htmlFor="txt-sobrenome" className="lbl-title">
                      Seu Sobrenome?*
                    </label>
                    <input onChange={this.handleChange}
                      type="text"
                      name="lastName"
                      value={this.state.lastName}
                      className="form-control border"
                      id="txt-sobrenome"
                      required
                    />
                  </div>
                </div>
                <div className="col-xl-6 col-lg-6 col-md-12 col-sm-12 col-12">
                  <div className="wrapper-content form-group">
                    <label htmlFor="txt-email" className="lbl-title">
                      Seu e-mail?*
                    </label>
                    <input onChange={this.handleChange}
                      type="email"
                      name="email"
                      value={this.state.email}
                      className="form-control border"
                      id="txt-email"
                      required
                    />
                  </div>
                </div>
                <div className="col-xl-6 col-lg-6 col-md-12 col-sm-12 col-12">
                  <div className="wrapper-content form-group">
                    <label htmlFor="txt-site" className="lbl-title">
                      Site da empresa?
                    </label>
                    <input onChange={this.handleChange}
                      type="site"
                      name="site"
                      value={this.state.site}
                      className="form-control border"
                      id="txt-site"
                      required
                    />
                  </div>
                </div>
                <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
                  <div className="wrapper-content form-group">
                    <label htmlFor="txt-empresa" className="lbl-title">
                      Nome da empresa?*
                    </label>
                    <input onChange={this.handleChange}
                      type="text"
                      name="company"
                      value={this.state.company}
                      className="form-control border"
                      id="txt-empresa"
                      required
                    />
                  </div>
                </div>

                <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
                  <div className="wrapper-content">
                    <label htmlFor="txt-necessidade" className="lbl-title">
                      Nos diga sua necessidade
                    </label>
                    <textarea onChange={this.handleChange}
                      name="need"
                      value={this.state.need}
                      id="txt-necessidade" className="form-control" />
                  </div>
                </div>
                <div className="wrapper-content">
                  <button className="button btn-send">Enviar</button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </form>
    );
  }

  handleChange = (e) => {
    this.setState({ [e.type] : e.target.value })
  }

}



// let FormOrcamentoServico = (props) => {
//   return (
//     <Mutation mutation={ADD_TODO}>
