import React, { Fragment } from "react";
import NavBar from "./../layouts/common/NavBar/NavBar";
import BannerTitle from "./../layouts/common/BannerTitle/BannerTitle";
import MapaSite from "./../layouts/common/MapaSite/MapaSite";
import RedesSociais from "./../layouts/common/RedesSociais/RedesSociais";
import Footer from "./../layouts/common/Footer/Footer";
import FormOrcamento from "./../forms/Orcamento/FormOrcamento";
import { Link } from "react-router-dom";
import Breadcrumb from './../layouts/common/Breadcrumb/Breadcrumb';
import PageComponent from "../core/PageComponent";

export default class Orcamento extends PageComponent {
  render() {
    return (
      <Fragment>
        {/* <!-- Configurção landscape --> */}
        <div id="lying" />

        {/* <!-- Configurção natural --> */}
        <div id="standing">
          <div id="wrapper-main" className="page-inside orcamento">
            
              <NavBar />
            

            <main>
              <section id="wrapper-banner">
                <BannerTitle>
                  <h1>
                    <strong>Orçamento</strong>
                  </h1>
                </BannerTitle>
              </section>

              <section id="wrapper-master">
                <Breadcrumb>
                  <li>
                    <Link to="/">dígitozero</Link>
                  </li>
                  <li>
                    <span>Orçamento</span>
                  </li>
                </Breadcrumb>

                <div className="master">
                  <div className="container">
                    <div className="row">
                      <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
                        <div className="wrapper-content">
                          <h3>
                            <strong>
                              Faremos um Check-up rápido para descobrir seus
                              interesses :D
                            </strong>
                          </h3>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>

                <div className="featured">
                  <div className="container">
                    <div className="row">
                      <p>
                        Lorem ipsum dolor sit amet, consectetur adipiscing elit.
                        Aenean enim nisi, consectetur nec dolor in, suscipit
                        efficitur tortor. Vivamus et ullamcorper massa. Fusce
                        auctor nunc nulla, non sollicitudin ligula tincidunt in.
                        Donec aliquet at orci eget aliquam. Duis in fermentum
                        nisl. Vivamus a lorem augue. Cras mattis arcu vitae
                        mollis vehicula.
                      </p>
                    </div>
                  </div>
                </div>

                <div className="section">
                  <FormOrcamento />
                </div>
              </section>

              <section id="wrapper-map" className="featured">
                <MapaSite />
              </section>

              <section id="wrapper-social-networks">
                <RedesSociais />
              </section>

              <footer id="wrapper-footer">
                <Footer />
              </footer>
            </main>
          </div>
        </div>
      </Fragment>
    );
  }
}



