import React, { Component } from "react";
import PostBlog from './../../Blog/PostBlog';

class NoticiasBlog extends Component {

  render() {
    return (
      <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
        <div className="row">
          <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
            <div className="wrapper-title-area">
              <h2 className="title area top">
                <span>{this.props.title}</span>
              </h2>
            </div>
          </div>

          {this.getPosts()}
        </div>
      </div>
    );
  }

  getPosts = () => {
    if(this.props.blogPostPreview){
      return this.props.blogPostPreview.map(post => (
        <div key={post._id} className="col-xl-4 col-lg-4 col-md-12 col-sm-12 col-12">
        <div className="wrapper-content news">
          <PostBlog {...post} />
        </div>
      </div>
    ))
  }
  }
}

export default NoticiasBlog;



