import React, { Fragment } from "react";
import BannerTitle from "../../layouts/common/BannerTitle/BannerTitle";
import Breadcrumb from "../../layouts/common/Breadcrumb/Breadcrumb";
import NavBar from "../../layouts/common/NavBar/NavBar";
import MapaSite from "../../layouts/common/MapaSite/MapaSite";
import RedesSociais from "../../layouts/common/RedesSociais/RedesSociais";
import Footer from "../../layouts/common/Footer/Footer";
import { Link } from "react-router-dom";
import PageComponent from "../../core/PageComponent";
import FormOrcamentoServico from "./../../forms/common/FormOrcamentoServico";
import DevelopmentSVG from "./DevelopmentSVG";

export default class Development extends PageComponent {
  render() {
    return (
      <Fragment>
        {/* <!-- Configurção landscape --> */}
        <div id="lying" />

        {/* <!-- Configurção natural --> */}
        <div id="standing">
          <div id="wrapper-main" className="page-inside site">
            
              <NavBar />
            

            <main>
              <section id="wrapper-banner">
                <BannerTitle>
                  <h1>
                    <strong>Desenvolvimento</strong>
                  </h1>
                </BannerTitle>
              </section>

              <section id="wrapper-master">
                <Breadcrumb>
                  <li>
                    <Link to="/">dígitozero</Link>
                  </li>
                  <li>
                    <span>Desenvolvimento</span>
                  </li>
                </Breadcrumb>

                {/* <div className="master">
                <div className="container">
                  <div className="row">
                    <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
                      <div className="wrapper-content">
                        <h3 />
                      </div>
                    </div>
                  </div>
                </div>
              </div> */}

                <div className="container">
                  <div className="row">
                    <div className="col-xl-6 col-lg-6 col-md-12 col-sm-12 col-12">
                      <div className="wrapper-content">
                        <span className="icon site">
                          <DevelopmentSVG />
                        </span>
                      </div>
                    </div>
                    <div className="col-xl-6 col-lg-6 col-md-12 col-sm-12 col-12">
                      <div className="wrapper-content">
                        <h2>WE MAKE WEBSITES</h2>
                        <p>
                          Sites Otimizados para SEO, com design impecável e
                          ótima usabilidade
                        </p>
                        <h2>WE MAKE WEBSITES</h2>
                        <p>
                          Faça seu aplicativo com a gente, fidelize seu cliente
                          com uma boa experiência!
                        </p>
                        <h2>WE MAKE e-Commerce</h2>
                        <p>
                          Sua loja virtual pensada estrategicamente. Ofereça a
                          melhor experiência de compra para o seu cliente e ele
                          voltará!
                        </p>
                      </div>
                    </div>
                  </div>
                </div>

                <div className="section">
                  <div className="container">
                    <div className="row">
                    </div>
                  </div>
                </div>
              </section>

              <section id="wrapper-map" className="featured">
                <MapaSite />
              </section>

              <section id="wrapper-social-networks">
                <RedesSociais />
              </section>
            </main>

            <footer id="wrapper-footer">
              <Footer />
            </footer>
          </div>
        </div>
      </Fragment>
    );
  }
}



